import { Field } from '#ui-kit3/components/Field';
import type { FieldOwnProps } from '#ui-kit3/components/Field';
import type { DateInputProps } from '#ui-kit3/components/input/DateInput';
import { DateInput } from '#ui-kit3/components/input/DateInput';
import * as React from 'react';
import { uid } from '#ui-kit3/components/common/uid';

export interface DateFieldProps extends DateInputProps, Omit<FieldOwnProps, 'inputRef'> {}

export const DateField = React.forwardRef<HTMLInputElement, DateFieldProps>((props, ref) => {
  const {
    className,
    displayInline,
    status,
    required,
    extraText,
    label,
    id = uid(),
    name,
    disabled,
    ...restProps
  } = props;

  const fieldContainerProps = {
    className,
    extraText,
    status,
    required,
    label,
    'data-field-id': id,
    'data-field-name': name,
    displayInline,
    disabled,
  } as Record<string, any>;

  (Object.keys(restProps) as Array<keyof typeof restProps>).forEach((key) => {
    if (key.startsWith('data-field')) {
      fieldContainerProps[key] = restProps[key];
    }
  });

  const inputProps = { ref, id, name, 'aria-required': required, status, disabled, ...restProps };
  return (
    <Field {...fieldContainerProps}>
      <DateInput {...inputProps} />
    </Field>
  );
});

DateField.displayName = 'DateField';
